:root {
  box-sizing: border-box;
  background-color: #282c34;
}

.App {
  text-align: center;
  background-color: #282c34;

  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.home {
  margin: 100px;
}

.link {
  color: white;
}
